import React from 'react';
import styles from './Article.module.css';
import VideoMP4 from '../videoMP4';
import YouTubeEmbed from '../YouTubeEmbed';

const page = () => {
  return (
    <div className={styles.article}>
        
        <div>
            <h3 className={styles.subtitle} >1. Qu'est-ce que le Générateur Procédural ?</h3>
            <p>Le terme "génératif procédural" se réfère à la création d'éléments ou de contenus de manière algorithmique, plutôt qu'en les concevant manuellement. En utilisant des algorithmes pour générer des environnements, des niveaux ou des cartes, on peut créer des résultats variés et uniques à chaque exécution du programme. Dans le contexte des jeux vidéo et des simulations, cela permet de produire des cartes et des niveaux qui ne sont pas seulement diversifiés mais également dynamiques et adaptatifs.</p>
        </div>

        <h3  className={styles.subtitle} >2. Carte de Bruit (Noise Map)</h3>
        
        <div className={styles.paragraph_50}>
            <p>Une carte de bruit est une technique utilisée pour générer des motifs aléatoires et naturels dans un espace donné. Dans ce projet, un générateur de bruit personnalisé est utilisé pour créer des cartes 2D. Cette méthode de bruit permet de produire des variations continues qui imitent des structures naturelles telles que les terrains, les océans, ou les régions montagneuses, offrant ainsi une base réaliste pour la génération de cartes procédurales.</p>
        </div>

        <div className={`${styles.paragraph_50} ${styles.margincontent}`}>
            <img className={styles.paragraph_50} src={process.env.PUBLIC_URL + '/data/images/noise.png'} alt="noise.png"></img>
        </div>

        <div>
            <h3 className={styles.subtitle} >3. Technique de Rendu d'une Carte 2D en Sphère</h3>
            <p>Pour afficher une carte 2D en projection sphérique, j'ai utilisé une technique inspirée du rendu de disque de SDL2. Cette approche consiste à prendre la carte 2D et à la mapper sur un disque en simulant une sphère. En utilisant les capacités de rendu de disque de SDL2, j'ai pu créer une illusion de profondeur et de courbure sphérique, donnant l'impression que la carte 2D est projetée sur une surface sphérique, même si en réalité, elle reste une représentation plane.</p>
        </div>
        <div className= {`${styles.paragraph_100} ${styles.margincontent}`}>
            <VideoMP4 filePath={`${process.env.PUBLIC_URL}/data/video/NoiseMap.mp4`} />
        </div>

        <h3 className={styles.subtitle} >4. Problèmes des Pôles et Solution avec une Carte en Cube</h3>
        <div className={styles.paragraph_50}>
            <p>Un problème commun avec la projection sphérique des cartes 2D est la distorsion aux pôles, où les éléments peuvent apparaître étirés ou déformés. Pour contourner ce problème, une solution consiste à utiliser une carte en cube. Cette méthode divise la surface sphérique en six faces planes qui forment un cube. Chaque face est mappée à partir de la carte 2D, permettant ainsi de réduire les distorsions aux pôles et d'obtenir un rendu plus uniforme de l'ensemble de la carte.</p>
        </div>
        <img className={styles.paragraph_50} src={process.env.PUBLIC_URL + '/data/images/TPWKz.png'} alt="mapcube.png"></img>
        
        <h3 className={styles.subtitle} >Pour aller Plus Loin</h3>
        <div className={styles.paragraph_100}>
            <p>Si vous êtes intéressé par une exploration plus approfondie des cartes de bruit et des techniques associées, voici quelques ressources et références que j'ai trouvées intéressantes lors de mes recherches récentes :</p>
        </div>
        <YouTubeEmbed videoId="gsJHzBTPG0Y" />
        <YouTubeEmbed videoId="eiM_AStmeGE" />

    </div>
  );
}

export default page;